import React, {
  useEffect
} from "react"
import {
  useQuery,
  gql
} from '@apollo/client';

const DATA_QUERY = gql `
  query GetHomeData {
    allMimHomePages {
      intro
      about
      facebook
      partners
    }
    allMimPeople(where: { showOnHome: true }) {
      name
      title
      bio
      image {
        publicId
      }
    }
  }
`;

function IndexPage() {

  useEffect(() => {
        document.onkeydown = (evt) => {
          console.log(evt.code);
          // (evt.keyCode === '')
        };
  });


      const { loading, error, data } = useQuery(DATA_QUERY);
      console.log(data);
      if (loading) return <div>Loading...</div>;
      if (error) return <div>Error...</div>;
    return (
    <main>
      <title>Home Page</title>
    </main>
  );
}

export default IndexPage
